import { IEventId } from '../interfaces';

export const EVENT_LOAD_DATA_START = 'LoadDataEventStart';
export const EVENT_LOAD_DATA_STOP = 'LoadDataEventStop';

/**
 * Additional properties to send along with ending a request
 */
export interface IEventLoadDataRenderProperties extends IEventId {
    /**
     * What initiated the load data request
     */
    initiator: 'ClientRender' | 'ServerRender' | 'MakeLoadDataCall' | 'Self';
}
